<template>
	<div class="search-view">
		<div class="separator-box">
			<el-breadcrumb class="separator" separator-class="el-icon-arrow-right">
				<el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item>{{ keyword }}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="goods-list" v-if="this.goodsList.length > 0">
			<goods-item v-for="goods in goodsList" :key="goods.id" :goods="goods"></goods-item>
		</div>
		<div class="else-box" v-else>
			<p>没有搜索到与“{{ keyword }}”的内容</p>
		</div>
	</div>
</template>

<script>
import { getSearchListApi } from '@/api/goods';
import GoodsItem from '@/components/GoodsItem';

export default {
	name: 'search-view',
	components: {
		GoodsItem,
	},
	data() {
		return {
			goodsList: [],
			keyword: '',
		};
	},
	activated() {
		this.getSearchList(this.$route);
	},
	// 当路由变化的时候导航到同一个页面，又使用了keep-alive，要使用组件内的路由，当路由发生变时，重新发请求
	beforeRouteUpdate(to, from, next) {
		// 路由跳转前后的对应的组件相同(路由切换前后,路由path相同)时,会执行路由更新这个生命周期函数,  如当前是搜索页,点击按钮仍然跳转到搜索页
		this.getSearchList(to);
		next();
	},
	methods: {
		async getSearchList(to) {
			this.keyword = to.query.keyword;
			let res = await getSearchListApi({ word: to.query.keyword });
			this.goodsList = res;
		},
	},
};
</script>

<style scoped>
.goods-list {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -10px;
}
.else-box {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 100px;
}
</style>
